  }
  .active2 {
    background-color: #ffffff;
    border-top: 3px solid #008fff;
  }
  .detail-content {
    box-sizing: border-box;
    height: 603px;
    width: 100%;
    color: #008fff;
    font-size: 18px;
    padding: 68px 49px 60px 63px;
    div {
      font-size: 16px;
      color: #8f8f8f;
      line-height: 24px;
      font-family: PingFangSC-Semibold;
      font-weight: 500px;
    }
    div:nth-child(1){
      margin-bottom: 20px;
    }
  }
  .share {
    position: absolute;
    right: 0;
    top: 0;
    color: #bebebe;
    font-size: 12px;
    i {
      font-style: normal;
      vertical-align: middle;
      margin-right: 11px;
    }
    img {
      vertical-align: middle;
      margin-right: 14px;
    }
  }
</style>



